import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import Home from './home/Home';
import { ROUTE_COLORS_PARAM, ROUTE_HOW_TO, ROUTE_NOT_FOUND } from './contatns';
import HowTo from './howTo/HowTo';
import NoMatch from './noMatch/NoMatch';

const Routes = () => {
    return (
        <Switch>
            <Route exact path={ROUTE_COLORS_PARAM} render={
                (props) => <Home {...props} />} />
            <Route exact path={ROUTE_HOW_TO} render={
                (props) => <HowTo {...props} />} />
            <Route path={ROUTE_NOT_FOUND} render={
                (props) => <NoMatch {...props} />} />
            <Redirect from='*' to={ROUTE_NOT_FOUND} />
        </Switch>
    );
};

export default Routes;


