import React from 'react';
import Close from '@material-ui/icons/Close';
import Settings from '@material-ui/icons/Settings';
import IconButton from '@material-ui/core/IconButton';
import { isDarkColor, hex2rgb } from '../../utils';
import { withStyles } from '@material-ui/core/styles';
import Tooltip from '@material-ui/core/Tooltip';

const chooseStyle = (orientation, size, index) => {
    if (orientation === 'horizontal') {
        return {
            width: size + '%',
            height: '100%',
            left: index * size + '%',
            'minHeight': '100px',
        }
    }

    return {
        top: index * size + '%',
        height: size + '%',
        left: 0,
        width: '100%'
    }
}

const styles = theme => ({
    fab: {
        margin: theme.spacing.unit * 2,
    },
    absolute: {
        position: 'absolute',
        bottom: theme.spacing.unit * 10,
        right: theme.spacing.unit * 5,
    },
    lightTooltip: {
        background: theme.palette.common.white,
        color: theme.palette.text.primary,
        boxShadow: theme.shadows[1],
        fontSize: 11,
    },
});

const Color = ({ hex, width, onRemove, active, index, orientation, onClickPopup, classes }) => {
    const classColor = isDarkColor(hex2rgb('#' + hex)) ? ' color-dark' : ' color-light';
    return (
        <div style={{
            'backgroundColor': `#${hex}`,
            'display': 'inline-block',
            'transition': 'all 0.2s ease-in-out',
            'position': 'absolute',
            ...chooseStyle(orientation, width, index)
        }} className={"color" + (active ? ' selected' : '') + classColor}>
            <div className="controls">
                <Tooltip title="Customize" classes={{ tooltip: classes.lightTooltip }} placement="bottom">
                    <IconButton size="small" onClick={e => onClickPopup(e.currentTarget, index)}>
                        <Settings />
                    </IconButton >
                </Tooltip>
                <Tooltip title="Remove" classes={{ tooltip: classes.lightTooltip }} placement="bottom">
                    <IconButton size="small" onClick={onRemove.bind(null, index)}>
                        <Close />
                    </IconButton >
                </Tooltip>
            </div>
        </div>
    );
};

export default withStyles(styles)(Color);


