import React, { Component } from 'react';
import Color from './Color';
import AddColor from './AddColor';
import CustomizeColorPopup from './CustomizeColorPopup';
import { ROUTE_COLORS } from '../../pages/contatns';
import { rgb2hex, getRandomArbitrary } from '../../utils';

class Colors extends Component {

    state = {
        orientation: 'horizontal',
        target: null,
        selectedIndex: null,
        selected: null
    }

    onAddColor = (color) => {
        const { match: { params }, history } = this.props;
        const { selectedIndex } = this.state;
        const colors = params.colors.split(',');
        if (selectedIndex >= 0) {
            colors[selectedIndex] = color;
        } else {
            colors.push(color);
        }
        history.push(`${ROUTE_COLORS}/${colors.join()}`);
    }

    onRemove = (index) => {
        const { match: { params }, history } = this.props;
        const colors = params.colors.split(',');
        colors.splice(index, 1);
        history.push(`${ROUTE_COLORS}/${colors.join()}`);
    }

    changeOrientation = () => {
        const orientation = this.state.orientation === 'horizontal' ? 'vertical' : 'horizontal';
        this.setState({
            orientation
        })
    }

    onChangeIndex = (target, index) => {
        const { match: { params } } = this.props;
        const colors = params.colors.split(',');
        this.setState({
            target: target,
            selectedIndex: index,
            selected: `#${colors[index]}`
        })
    }

    onCloseColorPopup = () => {
        this.setState({
            target: null,
            selectedIndex: null
        })
    }

    randomColor = () => {
        const r = getRandomArbitrary(0, 255);
        const g = getRandomArbitrary(0, 255);
        const b = getRandomArbitrary(0, 255);
        this.setState({
            selected: rgb2hex(r, g, b)
        })
    }

    render() {
        const { orientation, target, selectedIndex, selected } = this.state;
        const { match: { params } } = this.props;
        const colors = params.colors.split(',');
        return (
            <div className="colors">
                {/* <div style={{ zIndex: '1020', 'position': 'fixed', 'right': '100px', 'top': 0 }}>
                    <button type="button" onClick={this.changeOrientation}>Change orientation </button>
                </div> */}
                {colors.map((color, index) => <Color
                    key={`${color}`}
                    onClickPopup={this.onChangeIndex}
                    orientation={orientation}
                    onRemove={this.onRemove}
                    active={index === selectedIndex}
                    index={index}
                    hex={color}
                    width={100 / colors.length} />)}
                <AddColor onAddColor={this.onAddColor} onClickPopup={this.onChangeIndex} />

                <CustomizeColorPopup
                    onSaveColor={this.onAddColor}
                    target={target}
                    onClose={this.onCloseColorPopup}
                    randomColor={this.randomColor}
                    hex={selected} />
            </div>
        );
    }
}

export default Colors;


